import {
	hostUrl
} from '@/http/request.js';
import {
	cartAdd,
	cartDecrease,
	cartDel
} from '@/http/cart.js';
export default {
	props: {
		cartList: {
			type: Array,
			default: () => {
				return []
			}
		}
	},
	data() {
		return {
			deleteImg: '/static/images/cartImg/delete.png',
			hostUrl: hostUrl,

		}
	},
	mounted() {
			this.userId = uni.getStorageSync('userId');
			console.log(this.userId)
	},
	methods: {
		decrease(info) {
			let {id,quantity} = info;
			if(quantity <= 1){
				uni.showToast({
					title:'购买数量不能为0',
					icon:'none'
				})
				return
			}
			cartDecrease(id).then(res=>{
				if(res.data !== null && res.data.status === 0){
					this.$emit('changeCart')
				}
			})
		},
		add(info){
			let {quantity,stock,id} = info;
		
			if(quantity > stock){
				uni.showToast({
					title:'购买数量大于库存',
					icon:'none'
				})
				return
			}
			cartAdd(id).then(res =>{
				
				if(res.data !== null && res.data.status === 0){
					this.$emit('changeCart')
				}
			})
		},
		delItem(id){
			
			cartDel(id).then(res=>{
				if(res.data !== null && res.data.status === 0){
					this.$emit('changeCart')
				}
			})
		},
		choice(e) {
			let {
				id,
				checkid
			} = e.currentTarget.dataset;
			this.$emit('choice',{id,checkId:checkid})
		
		},
		goDetails(id){
			uni.navigateTo({
				url:`/pages/details/details?id=${id}`
			})
		}
	},
	computed: {

	}
}
